================================================== */
.showPc {
  display: block;
}
.showTa {
  display: none;
}
.showMo {
  display: none;
}
.showTaMo {
  display: none;
}

@media screen and (max-width: 1024px) {
  .showPc {
    display: none;
  }
  .showTa {
    display: block;
  }
  .showMo {
    display: none;
  }
  .showTaMo {
    display: block;
  }
}

@media screen and (max-width: 640px) {
  .pcArea {
    display: none;
  }
  .showPc {
    display: none;
  }
  .showTa {
    display: none;
  }
  .showMo {
    display: block;
  }
  .showTaMo {
    display: block;
  }
}

