/*#header{width:100%; position:relative;  min-width:1200px; margin:0 auto; background:#2d2d2f; padding:15px 20px; box-sizing:border-box; overflow:hidden; height:80px;}*/
.size {
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
  padding: 0 40px;
  box-sizing: border-box;
}
#header.main .inner .top {
  position: relative;
  height: 135px;
  text-align: center;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  transition: all 0.3s;
  -webkit-transition: all 0.3s;
}
#header.main .inner .top .logo {
  display: inline-block;
  padding-top: 28.5px;
}
#header.main .inner .top .logo a {
  display: block;
}
#header.main .inner .top .left {
  position: absolute;
  top: 49.5px;
  left: 40px;
}
#header.main .inner .top .left a {
  display: inline-block;
  font-size: 24px;
  font-weight: bold;
  color: #fff;
}
#header.main .inner .top .left a img {
  padding-right: 5px;
}
#header.main .inner .top .right {
  position: absolute;
  top: 42.5px;
  right: 40px;
}
#header.main .inner .top .right .util a:first-child {
  margin-right: 35px;
}
#header.main .inner .top .right .util a {
  float: left;
  font-size: 14px;
  color: #fff;
  font-weight: 400;
  text-align: center;
}
#header.main .inner .top .right .util a img {
  display: block;
  margin: 0 auto;
  padding-bottom: 10px;
}
#header.main .inner .gnb {
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  box-sizing: border-box;
  background-color: rgb(27 16 0 / 50%);
}
#header.main .inner .gnb .depth1 > li {
  float: left;
  width: 24%;
  position: relative;
}
#header.main .inner .gnb .depth1 > li:nth-child(5) {
  display: none;
} /*    */
#header.main .inner .gnb .depth1 > li > a {
  display: block;
  height: 50px;
  line-height: 50px;
  font-size: 17px;
  color: #fff;
  text-align: center;
  font-weight: bold;
}
#header.main .inner .gnb .depth2 {
  display: none;
  z-index: 10;
  width: 100%;
  height: 360px;
  padding: 20px 0;
  box-sizing: border-box;
  position: absolute;
  top: 50px;
}
#header.main .inner .gnb .depth2 > li > a {
  display: block;
  height: 40px;
  line-height: 40px;
  font-size: 16px;
  text-align: center;
  color: #333;
}
.header_bg {
  background: #fff;
  height: 0;
  position: absolute;
  top: 187px;
  display: block;
  width: 100%;
}
#header.main .inner .gnb.on {
  background: #fff;
  border-bottom: 1px solid #f2f2f2;
}
#header.main .inner .gnb.on .depth1 > li > a {
  color: #333;
}
#header.main .inner .gnb .depth1 > li > a span {
  display: inline-block;
  position: relative;
}
#header.main .inner .gnb .depth1 > li > a.on {
  color: #206d43;
}
#header.main .inner .gnb .depth1 > li > a.on > span:after {
  width: 100%;
  height: 1px;
  background: #206d43;
  position: absolute;
  bottom: -0.5px;
  left: 0;
  content: "";
}
#header.main .inner .gnb .depth2 > li > a:hover {
  color: #206d43;
}

#header.main .inner .top .mo {
  display: none;
}

.mo_allMenu {
  z-index: 99;
  position: fixed;
  left: -200%;
  top: 0;
  width: 100%;
  height: 100%;
}
.mo_allMenu.on {
  overflow: auto;
  height: 100%;
}

.m_bg {
  position: fixed;
  display: none;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.85);
  z-index: 90;
}
.mo_allMenu .mo_inner {
  width: calc(100% - 70px);
  height: 100%;
  background: #fff;
}
.mo_allMenu .all_top .m_close {
  position: absolute;
  right: 0;
  width: 70px;
  top: 0;
  height: 65px;
  /* background: url("/img/mo_close.png") no-repeat center center; */
  background-size: 50%;
  margin-left: 0;
  padding: 0;
}
.mo_allMenu .all_top {
  background: #206d43;
  height: 65px;
  line-height: 65px;
  padding: 0 20px;
  box-sizing: border-box;
}
.mo_allMenu .all_top a {
  display: inline-block;
  margin-left: 20px;
  /* background: url("/img/join_icon.png") no-repeat left center; */
  color: #fff;
  font-size: 22px;
  font-weight: 400;
  text-align: left;
  padding-left: 25px;
  box-sizing: border-box;
}
.mo_allMenu .all_top a:first-child {
  margin-left: 0;
  /* background: url("/img/login_icon.png") no-repeat left center; */
}
.mo_allMenu .mgnb .m_depth1 > li:nth-child(5) {
  display: none;
} /*    */
.mo_allMenu .mgnb .m_depth1 > li > a.on {
  background: #f4cb00;
  border-bottom: 0;
}
.mo_allMenu .mgnb .m_depth1 > li > a.on:after {
  /* background: url("/img/mo_menu_over.png") no-repeat center center; */
  background-size: 35%;
}
.mo_allMenu .mgnb .m_depth1 > li > a {
  border-bottom: 1px solid #e4e4e4;
  display: block;
  position: relative;
  height: 55px;
  line-height: 55px;
  background: #fff;
  font-size: 25px;
  font-weight: 500;
  padding: 0 20px;
  box-sizing: border-box;
}
.mo_allMenu .mgnb .m_depth1 > li > a:after {
  position: absolute;
  content: "";
  /* background: url("/img/mo_menu.png") no-repeat center center; */
  width: 55px;
  height: 55px;
  top: 0;
  right: 0;
  background-size: 35%;
}
.mo_allMenu .mgnb .m_depth2 {
  display: none;
}
.mo_allMenu .mgnb .m_depth2 > li > a {
  display: block;
  height: 50px;
  line-height: 50px;
  background: #333;
  padding: 0 20px;
  box-sizing: border-box;
  color: #fff;
  font-size: 23px;
  font-weight: 300;
}
.mo_allMenu .mgnb .m_depth2 > li {
  border-bottom: 1px solid rgba(0, 0, 0, 0.7);
}

.m_sub_tab {
  display: block;
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 9;
}
.m_sub_tab .m_sub_depth {
  width: 50%;
  float: left;
  position: relative;
}
.m_sub_tab .m_sub_depth > a {
  display: block;
  text-align: left;
  border-right: 1px solid #548969;
  background: #206d43;
  color: #fff;
  padding: 0 15px;
  height: 55px;
  line-height: 55px;
  box-sizing: border-box;
  font-weight: bold;
  font-size: 18px;
}
.m_sub_tab .last > a {
  border-right: 0;
}
.m_sub_tab .m_sub_depth > a:after {
  position: absolute;
  display: block;
  /* background: url("/img/mo_tab.png") no-repeat center center; */
  background-size: 30%;
  width: 55px;
  height: 55px;
  content: "";
  right: 0;
  top: 0;
}
.m_sub_tab .m_sub_depth > ul > li > a {
  display: block;
  text-align: left;
  background: #333;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-weight: 500;
  color: #fff;
  font-size: 18px;
  display: block;
  padding: 0 15px;
  height: 45px;
  line-height: 45px;
  box-sizing: border-box;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}
.m_sub_tab .m_sub_depth > ul > li:first-child > a {
  border-top: 0;
}
.m_sub_tab .m_sub_depth > ul > li > a.on {
  background: #f4cb00;
  color: #fff;
  font-weight: bold;
}
.m_sub_tab .m_sub_depth > ul {
  display: none;
}
.m_sub_tab .m_sub_depth > a.on:after {
  /* background: url("/img/mo_tab.png") no-repeat center center; */
  background-size: 30%;
}

