#footer {
  width: 100%;
  position: relative; /*min-width:1200px; */
  margin: 0 auto;
  text-align: center;
}
#footer .quick.right {
  position: fixed;
  right: 20px;
  z-index: 1;
  bottom: 3%;
}

#warp .footer {
  width: 100%;
  position: relative;
  min-width: 1000px;
  margin: 0 auto;
  padding: 20px 0;
  text-align: center;
  font-size: 12px;
  color: #a7a8a9;
}
#warp .footer p {
  text-align: center;
}
#footer .footer p span {
  font-weight: bold;
}

.tb {
  display: table;
  width: 100%; /*height: 100px;*/
}
.tbc {
  display: table-cell;
  vertical-align: middle;
}
#footer {
  background: #333;
}
#footer .top {
  border-bottom: 1px solid #484848;
  box-sizing: border-box;
  padding: 30px 0;
  transition: all 0.3s;
  -webkit-transition: all 0.3s;
}
#footer .size {
  max-width: 1930px;
  padding: 0 40px;
  box-sizing: border-box;
  margin: 0 auto;
  position: relative;
}
#footer .top .size .inner {
  position: relative;
}
#footer .top .size .left {
  float: left;
  min-width: 320px;
}
#footer .top .size .left p {
  position: relative;
}
#footer .top .size .left p > b {
  position: absolute;
  left: 0;
}
#footer .top .size .left b {
  line-height: 1;
  font-size: 16px;
  color: #fff;
  font-weight: 500;
  box-sizing: border-box;
}
#footer .top .size .left span {
  line-height: 1;
  padding-left: 70px;
  box-sizing: border-box;
  font-size: 38px;
  color: #f4cb00;
  font-weight: 900;
  float: left;
}
#footer .top .size .left.time span {
  padding-left: 110px;
  text-align: left;
  line-height: 20px;
  font-size: 16px;
  color: #fff;
  font-weight: 500;
  box-sizing: border-box;
  float: none;
  display: block;
}
#footer .top .size .sns {
  position: absolute;
  right: 0;
  top: 50%;
  margin-top: -20px;
}
#footer .top .size .sns a {
  display: block;
  float: left;
  /* background: url("/img/blog.png") no-repeat center center; */
  width: 40px;
  height: 40px;
  background-size: 70%;
}
#footer .top .size .sns a:first-child {
  margin-right: 5px;
  /* background: url("/img/facebook.png") no-repeat center center; */
}
#footer .info {
  border-bottom: 1px solid #484848;
  box-sizing: border-box;
  padding: 30px 0;
  transition: all 0.3s;
  -webkit-transition: all 0.3s;
}
#footer .info .size .inner {
  position: relative;
  /* background: url("/img/f_logo.png") no-repeat left top; */
  text-align: left;
  padding-left: 75px;
}
#footer .info .size .inner > b {
  font-size: 18px;
  line-height: 1;
  font-weight: 500;
  color: #fff;
  display: inline-block;
}
#footer .info .size .inner p {
  padding: 15px 0 5px 0;
  box-sizing: border-box;
}
#footer .info .size {
  position: relative;
}
#footer .info .size .inner > p span {
  font-size: 15px;
  line-height: 1;
  font-weight: 400;
  color: #fff;
  padding-left: 10px;
}
#footer .info .size .inner > p > span:first-child {
  padding-left: 0;
}
#footer .info .size .inner > .copy {
  color: rgba(255, 255, 255, 0.5);
  font-size: 14px;
  font-weight: 400;
}
#footer .info .size .inner .util {
  position: absolute;
  top: 0;
  right: 0;
}
#footer .info .size .inner .util a {
  display: inline-block;
  color: #fff;
  font-size: 16px;
  box-sizing: border-box;
  font-weight: 400;
  float: left;
  position: relative;
  padding-left: 15px;
}
#footer .info .size .inner .util a:first-child {
  padding-left: 0;
  padding-right: 15px;
}
#footer .info .size .inner .util a:first-child:after {
  position: absolute;
  content: "";
  width: 1px;
  height: 14px;
  top: 50%;
  margin-top: -7px;
  right: -0.5px;
  background: #717171;
}

/* ==================================================
	skip
================================================== */
a.skip {
  display: block;
  position: absolute;
  top: -1px;
  z-index: 999;
  width: 0;
  height: 0;
  overflow: hidden;
  text-align: center;
}
a.skip:active,
a.skip:focus {
  z-index: 999;
  width: 100%;
  height: auto;
  padding: 5px;
  background: #fff;
}

