  /* background: #fff url(/img/common/caret_down.png) no-repeat right 13px center; */
}

.select_wrap select::-ms-expand {
  display: none;
}

.radio_wrap {
  position: relative;
}

.radio_wrap input[type="radio"] {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.radio_wrap label {
  position: relative;
  display: block;
  width: 100%;
  padding: 0 0 0 29px;
  box-sizing: border-box;
  font-size: 20px;
  font-weight: 900;
  cursor: pointer;
}

.radio_wrap label:after {
  content: "";
  display: block;
  clear: both;
}

.radio_wrap label .bullet {
  display: block;
  width: 22px;
  height: 22px;
  border: 2px solid #dadada;
  box-sizing: border-box;
  border-radius: 50%;
  position: absolute;
  top: 4px;
  left: 0;
}

.radio_wrap label > strong {
  float: left;
  display: inline-block;
  padding: 0 13px 0 0;
  text-align: left;
  color: #999;
}

.radio_wrap label > span:not(.bullet) {
  float: right;
  display: inline-block;
  color: #206d43;
}

.radio_wrap input[type="radio"]:checked + label .bullet {
  border-color: #206d43;
}

.radio_wrap input[type="radio"]:checked + label .bullet:after {
  content: "";
  display: block;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #206d43;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -4px 0 0 -4px;
}

.radio_wrap input[type="radio"]:checked + label strong {
  color: #000;
}

